import * as React from "react";
import Layout from "../components/Layout";
import { Link, graphql } from "gatsby";

const IndexPage = ({ data }) => {
  const posts = data.allMdx.nodes;

  return (
    <Layout>
      <main className="flex flex-col sm:w-full md:w-[768px] mt-3 m-auto px-4 py-4">
        {posts.map((mdx) => {
          return (
            <article key={mdx.id} className="py-8 border-b border-slate-200">
              <Link className="group" to={`/posts/${mdx.frontmatter.slug}`}>
                <h2 className="text-2xl font-bold text-violet-900 group-hover:underline">
                  {mdx.frontmatter.title}
                </h2>
                <p className="mt-2 text-base text-slate-600">
                  {mdx.frontmatter.description}
                </p>
              </Link>

              <div className="flex items-center gap-4 mt-4 text-sm">
                <span className="text-slate-500">{mdx.frontmatter.date}</span>
                <div className="flex flex-wrap gap-2">
                  {mdx.frontmatter.tags &&
                    mdx.frontmatter.tags.map((tag) => {
                      return (
                        <span className="inline-block rounded-md px-2 py-1 text-xs font-medium bg-slate-100 text-slate-700">
                          #{tag}
                        </span>
                      );
                    })}
                </div>
              </div>
            </article>
          );
        })}
      </main>
    </Layout>
  );
};

export const query = graphql`
  query MyQuery {
    allMdx(
      sort: { frontmatter: { date: DESC } }
      filter: { internal: { contentFilePath: { regex: "/src/posts/" } } }
    ) {
      nodes {
        frontmatter {
          date(formatString: "MMMM D, YYYY")
          title
          description
          slug
          tags
        }
        id
      }
    }
  }
`;

export const Head = ({ data }) => {
  return (
    <>
      <title>jongseong's blog</title>
    </>
  );
};

export default IndexPage;
